<template>
  <b-container fluid>
    <b-row>
      <b-col>
      <b-btn size="sm" class="" variant="primary" @click="login()">
        Login
      </b-btn>
      <b-btn size="sm" class="" variant="primary" @click="save()">
        Save
      </b-btn>
      <b-btn size="sm" class="" variant="primary" @click="load()">
        Load
      </b-btn>
      {{status}}

      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import { Data } from './data.js'
import queryString from 'querystring'
export default {
  name: 'dropbox',
  data () {
    return {
      status: ''
    }
  },
  methods: {
    save () {
      this.status = 'Saving...'
      Data.saveToDropbox().then(() => {
        this.status = 'Done'
      }).catch(() => { this.status = 'Error' })
    },
    load () {
      this.status = 'Loading...'
      Data.loadFromDropbox().then(() => {
        this.$emit('loaded')
        this.status = 'Done'
      }).catch(() => { this.status = 'Error' })
    },
    login () {
      var url = Data.login(window.location)
      window.location = url
    },
    isAuthenticated () {
      return Data.isAuthenticated()
    }
  },
  mounted () {
    console.log(this.$route)
    if (this.$route.hash) {
      var token = queryString.parse(this.$route.hash.substr(1)).access_token
      console.log('setting token to ', token)
      Data.setToken(token)
    } else {
      Data.loadToken()
    }
  }
}
</script>



