import Vue from 'vue'
import Router from 'vue-router'
import Flashcard from '@/components/Flashcard'
import Folders from '@/components/Folders'
import Study from '@/components/Study'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'overview',
      component: Folders
    },
    {
      path: '/flashcard/:id',
      name: 'flashcard',
      component: Flashcard
    },
    {
      path: '/study/:folderid',
      name: 'study',
      component: Study
    }
  ],
  mode: 'history'
})



