<template>
  <b-container>
    <b-row class="justify-content-md-center" v-if="!done">
      {{this.indx+1}} out of {{this.fcs.length}} (Avg. Score: {{this.fcs[this.indx].score}})
    </b-row>
    <b-row class="justify-content-md-center">
      <router-link :to="{name: 'overview'}" tag="b-button">menu</router-link>
    </b-row>
    <b-row class="justify-content-md-center">
      <b-col md="auto" @click="flip()">
    <draw v-if="!flipped&&!done" id="front" :svg="fcs[indx].frontSVG" :editmode="false" ></draw>
      </b-col>

    </b-row>
    <b-row v-if="done" class="justify-content-md-center">
      <h1>Done with this deck</h1>
    </b-row>
    <b-row v-if="flipped&&!done" class="justify-content-md-center">
      <b-col>
        <b-button-group>
          <b-button variant="danger" @click="saveResult(-2)">Bad</b-button>
          <b-button variant="secondary" @click="saveResult(-1)">Kinda bad</b-button>
          <b-button variant="secondary-outline" @click="saveResult(0)">Skip</b-button>
          <b-button variant="secondary" @click="saveResult(1)">Kinda good</b-button>
          <b-button variant="success" @click="saveResult(2)">Good</b-button>
        </b-button-group>
      </b-col>
    </b-row>
    <b-row v-if="flipped" class="justify-content-md-center">
      <b-col md="auto">
    <draw id="back" :svg="fcs[indx].backSVG" :editmode="false"></draw>
      </b-col>

    </b-row>
    <b-row>
      <b-col>


      </b-col>
    </b-row>
  </b-container>

</template>

<script>
// import {fabric} from 'fabric-browseronly'
import { Data } from './data.js'
export default {
  name: 'app',
  data () {
    return {
      fcs: [0],
      indx: 0,
      flipped: false
    }
  },
  watch: {
    '$route' (to, from) {
      this.load()
    }
  },
  mounted () {
    this.load()
  },
  methods: {
    flip () {
      this.flipped = true
    },
    load () {
      this.fcs = Data.getFlashcardsInFolderRandom(this.$route.params.folderid)
    },
    nextCard () {
      this.flipped = false
      if (this.indx === this.fcs.length - 1) {
        this.done = true
      } else {
        this.indx++
      }
    },
    saveResult (score) {
      if (score !== 0) {
        Data.addScoreAndSaveFlashcard(this.fcs[this.indx], score).then(this.nextCard)
      } else {
        this.nextCard()
      }
    }
  }
}
</script>

<style>
</style>



