<template>
  <div>
    <b-button-group v-if="editmode">
      <b-button @click="pen('black')">P (black)</b-button> 
      <b-button @click="pen('red')">P (red)</b-button> 
      <b-button @click="pen('green')">P (green)</b-button> 
      <b-button @click="eraser()">E</b-button> 
      <b-button @click="selection()">S</b-button> 
    </b-button-group>
    <br>
    <canvas hidpi="on" v-if="editmode" :id="id" width="840" height="600"></canvas>

    <span v-html="svg" v-else-if="svg"></span>
    <svg v-else height="600" width="800"></svg>
  </div>
</template>

<script>

import {paper as paper2} from 'paper'
import Pen from './pen.js'
import Eraser from './eraser.js'
import Selection from './selection.js'
      /*eslint-disable*/
var papers = {}
var paper
      /*eslint-enable*/
/*eslint-disable */
export default {
  name: 'draw',
  props: ['id', 'json', 'editmode', 'svg'],
  data () {
    return {
      mypaper: {}
    }
  },
  mounted () {
    document.getElementById(this.id).addEventListener('touchstart', (event) => {
      if (event.touches[0].touchType !== 'stylus') {
        event.stopPropagation()
        event.preventDefault()
      }
    })
    document.getElementById(this.id).addEventListener('touchmove', (event) => {
      if (event.touches[0].touchType !== 'stylus') {
        event.stopPropagation()
        event.preventDefault()
      }
    })
    if (this.editmode) {
      /*eslint-disable*/
      papers[this._uid] = new paper2.PaperScope()
      paper = papers[this._uid]
      /*eslint-enable*/
      paper2.install(window)
      paper.setup(document.getElementById(this.id))
    }
  },
  watch: {
    json (val) {
      paper = papers[this._uid]
      paper.activate()
      if (val === 'save') {
        this.$emit('jsonReady', {id: this.id, json: JSON.stringify(paper.project), svg: (paper.project.exportSVG({asString: true}))})
      } else if (this.editmode) {
        this.load(val)
      }
    }
  },
  methods: {
    load (json) {
      console.log(json)
      paper = papers[this._uid]
      paper.activate()
      paper.project.clear()
      paper.importJSON(json)
    },
    selection () {
      paper = papers[this._uid]
      paper.activate()
      var selection = new Selection(paper)
      selection.getTool().activate() 
    },
    pen (color) {
      paper = papers[this._uid]
      paper.activate()
      var pen = new Pen(paper, color)
      pen.getTool().activate() 
    },
    eraser () {
      paper = papers[this._uid]
      paper.activate()
      var eraser = new Eraser(paper)
      eraser.getTool().activate()
    }
  }
}
</script>

<style>
canvas,svg {
  outline: black 1px solid;
}
body, html {
    overflow-x: hidden;
    overflow-y: auto;
}
</style>



