<template>
  <b-container fluid>
    <b-row v-for="folder in folders">
      <b-col>
      <h3 >
        {{folder.name}} ({{folder.count}})
      </h3>
      </b-col>
      <b-col>
      <b-button @click="studyFolder(folder)" v-if="folder.count" variant="primary" size="sm">Study</b-button>
      <b-button @click="browseFolder(folder)" v-if="folder.count" variant="secondary" size="sm">Browse</b-button>
      <b-button @click="addFlashcard(folder)" variant="outline-secondary" size="sm">Add Flashcard</b-button>
      <b-button @click="editFolder(folder)" variant="outline-secondary" size="sm">Edit</b-button>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
      <b-btn size="sm" class="" variant="primary" @click="newFolder()">
        New Folder
      </b-btn>
      <dropbox @loaded="reloadData()"></dropbox>
      </b-col>
    </b-row>

  <b-modal ref="editFolderModal" title="Edit Folder">
    <b-input v-model="folderToEdit.name"></b-input>
    <div slot="modal-footer" class="w-100">
      <b-btn size="sm" class="float-right" variant="primary" @click="saveFolders()">
      Ok
      </b-btn>
    </div>
  </b-modal>
  </b-container>
</template>
<script>
import { Data } from './data.js'

export default {
  name: 'folders',
  data () {
    return {
      folders: [],
      folderToEdit: {}
    }
  },
  methods: {
    addFlashcard (folder) {
      Data.newFlashcard(folder).then((fc) => { this.$router.push({name: 'flashcard', params: { id: fc.id }}) })
    },
    studyFolder (folder) {
      this.$router.push({name: 'study', params: { folderid: folder.id }})
    },
    browseFolder (folder) {
      var fcid = Data.getFlashcardsInFolder(folder.id)[0].id
      this.$router.push({name: 'flashcard', params: { id: fcid }})
    },
    editFolder (folder) {
      this.folderToEdit = folder
      this.$refs.editFolderModal.show()
    },
    saveFolders () {
      Data.saveFolders(this.folders)
      this.$refs.editFolderModal.hide()
    },
    newFolder () {
      Data.newFolder().then(f => { this.folders = f })
    },
    reloadData () {
      Data.getFolders().then(f => { this.folders = f })
    }
  },
  mounted () {
    Data.getFolders().then(f => { this.folders = f })
  }
}
</script>



