<template>
  <div id="app">
    <router-view></router-view>
  </div>


</template>

<script>
import fastclick from 'fastclick'
export default {
  name: 'app',
  mounted () {
    fastclick.attach(document.body)
  }
}
</script>

<style>
#app {
  width: 100%;
  font-family: 'avenir', helvetica, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



