
/*eslint-disable*/
export default class {
  constructor (paper) {
    this.paper = paper
    this.firstCorner = 0
    this.secondCorner = 0
    this.group = null
    this.selectionRect = null
    this.selectionRectPath = null


  }
  onMouseDown (event) {
    if (this.group && this.group.bounds.contains(event.point)) {
      this.group.position = event.point
    } else {
      if (this.group) {
        this.group.selected = false
        this.selectionRectPath.remove()
      }

      this.group = null
      this.selectionRect = new this.paper.Rectangle(this.firstCorner, this.firstCorner)
      this.selectionRectPath = new paper.Path.Rectangle(this.selectionRect)
      this.firstCorner = event.point
    }
  }
  onMouseDrag (event) {
    if (!this.group) {
      this.secondCorner = event.point

      this.selectionRectPath.remove()
      this.selectionRect = new this.paper.Rectangle(this.firstCorner, this.secondCorner)
      this.selectionRectPath = new paper.Path.Rectangle(this.selectionRect)
      this.selectionRectPath.strokeColor = 'black';
      this.selectionRectPath.strokeWidth = 1;
      this.selectionRectPath.strokeCap = 'round';
      this.selectionRectPath.dashArray = [10, 12];
    }
  // Every drag event, add a point to the path at the current
  // position of the mouse:
    if (this.group && this.group.bounds.contains(event.point)) {
      this.group.position = event.point
    }
  }
  onMouseUp (event) {
    if (!this.group) {
      this.secondCorner = event.point
      var items = this.paper.project.getItems({ inside: new paper.Rectangle(this.firstCorner, this.secondCorner) })
      var group = new paper.Group(items)
      this.group = group
    }

    // items.forEach((it) => { it.selected = true })
  }
  getTool () {
      var tool = new this.paper.Tool()
      tool.onMouseDown = this.onMouseDown.bind(this)
      tool.onMouseDrag = this.onMouseDrag.bind(this)
      tool.onMouseUp = this.onMouseUp.bind(this)

      return tool
  }
/*eslint-enable */
}



