import { Dropbox } from 'dropbox'
import pako from 'pako'

var dbx = new Dropbox({ clientId: 'jzrc8jsz6h9fd4t' })
class DataClass {
  constructor () {
    this.staticVariable = 'asdf'
    this.dxKey = ''
    this.dx = {}
    this.ls = window.localStorage
    // this.saveFolders(([{name: 'Test Folder'}, {name: 'Test Folder 2'}]))
  }
  lsToString () {
    var fldrs = this.ls.getItem('folders')
    fldrs = !fldrs ? [] : JSON.parse(fldrs)

    var fcs = (this.ls.getItem('flashcards'))
    fcs = (!fcs) ? [] : JSON.parse(fcs)

    return pako.deflate(JSON.stringify({folders: fldrs, fcs: fcs}), {to: 'string'})
  }
  saveToDropbox () {
    return new Promise((resolve, reject) => {
      this.dx.filesUpload({path: '/fcs.json.gz', contents: this.lsToString(), mode: {'.tag': 'overwrite'}})
            .then(function (response) {
              resolve(response)
              console.log(response)
            })
            .catch(function (error) {
              reject(error)
              console.error(error)
            })
    })
  }
  loadFromDropbox () {
    return new Promise((resolve, reject) => {
      this.dx.filesDownload({path: '/fcs.json.gz'})
            .then((response) => {
              console.log(response)
              var fr = new FileReader()
              fr.onload = (e) => {
                console.log(e)
                var str = pako.inflate(fr.result, { to: 'string' })
                var json = JSON.parse(str)
                console.log(json)
                this.saveFolders(json.folders)
                this.saveFlashcards(json.fcs)
                resolve()
              }
              fr.readAsText(response.fileBlob)
            })
            .catch(function (error) {
              console.error(error)
            })
    })
  }
  setToken (token) {
    this.dxKey = token
    this.ls.setItem('dxkey', this.dxKey)
    this.dx = new Dropbox({accessToken: this.dxKey, clientId: 'jzrc8jsz6h9fd4t'})
  }
  loadToken () {
    if (this.ls.getItem('dxkey')) {
      this.dxKey = this.ls.getItem('dxkey')
      this.dx = new Dropbox({accessToken: this.dxKey, clientId: 'jzrc8jsz6h9fd4t'})
    }
  }
  isAuthenticated () {
    return this.dxKey
  }
  login (url) {
    return dbx.getAuthenticationUrl(url)
  }
  getFolders () {
    console.log(this)
    return new Promise((resolve, reject) => {
      var fldrs = this.ls.getItem('folders')
      fldrs = !fldrs ? [] : JSON.parse(fldrs)
      fldrs.forEach((fc, indx) => { fc.count = (this.getFlashcardsInFolder(fc.id)).length })
      resolve(fldrs)
    })
  }
  saveFolders (folders) {
    this.ls.setItem('folders', JSON.stringify(folders))
  }
  newFolder () {
    console.log(this)
    return new Promise((resolve, reject) => {
      this.getFolders().then(folders => {
        folders.push({id: Date.now(), name: 'new folder'})
        this.saveFolders(folders)
        resolve(folders)
      })
    })
  }
  getFlashcards () {
    var fcs = (this.ls.getItem('flashcards'))
    fcs = (!fcs) ? [] : JSON.parse(fcs)
    return fcs
  }
  shuffleArray (arr) {
    for (var i = arr.length - 1; i > 0; i--) {
      var j = Math.floor(Math.random() * (i + 1))
      var temp = arr[i]
      arr[i] = arr[j]
      arr[j] = temp
    }
    return arr
  }
  getFlashcardsInFolderRandom (folder) {
    return this.shuffleArray(this.getFlashcardsInFolder(folder))
  }
  getFlashcardsInFolder (folder) {
    var result = []
    var fcs = this.getFlashcards()
    fcs.forEach((fc, indx) => { if (fc.folder - folder === 0) { result.push(fc) } })
    return result
  }
  getFlashcard (id) {
    console.log(id)
    return new Promise((resolve, reject) => {
      this.getFlashcards().forEach((fc, indx) => { if (fc.id - id === 0) { resolve(fc) } })
    })
  }
  getInSeqFlashcard (id, prev) {
    return new Promise((resolve, reject) => {
      var result = 0
      this.getFlashcard(id).then((fc) => {
        var fcs = this.getFlashcardsInFolder(fc.folder)
        fcs.forEach((fc, indx) => { if (fc.id - id === 0) { result = (prev) ? indx - 1 : indx + 1 } })
        resolve((result >= 0 && result < fcs.length) ? fcs[result] : null)
      })
    })
  }
  saveFlashcards (flashcards) {
    this.ls.setItem('flashcards', JSON.stringify(flashcards))
  }
  saveFlashcard (fcEdited) {
    console.log(fcEdited.id)
    var fcs = this.getFlashcards()
    return new Promise((resolve, reject) => {
      fcs.forEach((fc, indx) => { if (fc.id - fcEdited.id === 0) { fcs[indx] = fcEdited } })
      this.saveFlashcards(fcs)
      resolve()
    })
  }
  addScoreAndSaveFlashcard (fc, score) {
    if (!fc.scores) {
      fc.scores = []
    }
    fc.scores.push(score)
    fc.score = fc.scores.reduce((a, b) => a + b) / fc.scores.length
    return this.saveFlashcard(fc)
  }
  newFlashcard (folder) {
    console.log(this)
    return new Promise((resolve, reject) => {
      var fcs = this.getFlashcards()
      var newFc = {id: Date.now(), folder: folder.id, front: '{}', back: '{}', score: 0, frontSVG: '', backSVG: ''}
      fcs.push(newFc)
      this.saveFlashcards(fcs)
      resolve(newFc)
    })
  }
  hello () {
    alert(this.staticVariable)
  }
}
export const Data = new DataClass()



