<template>
  <b-container>
    <b-row class="justify-content-md-center">
      <router-link :to="{name: 'overview'}" tag="b-button">menu</router-link>
      <router-link :to="{name: 'flashcard', params: {'id': prev }}" :disabled="prev==0" tag="b-button">previous</router-link>
      <router-link :to="{name: 'flashcard', params: {'id': next }}" :disabled="next==0" tag="b-button">next</router-link>
      <b-button size="sm" variant="primary" v-on:click="save()">
        save
      </b-button>
    </b-row>
    <b-row class="justify-content-md-center">
      <b-col md="auto">
    <draw id="front" :json="jFront" :svg="fc.frontSVG" @jsonReady="jsonReady" :editmode="true"></draw>
      </b-col>

    </b-row>
    <b-row class="justify-content-md-center">
      <b-col md="auto">
    <draw id="back" :json="jBack" :svg="fc.backSVG" @jsonReady="jsonReady" :editmode="true"></draw>
      </b-col>

    </b-row>
    <b-row>
      <b-col>


      </b-col>
    </b-row>
  </b-container>

</template>

<script>
// import {fabric} from 'fabric-browseronly'
import { Data } from './data.js'
export default {
  name: 'app',
  data () {
    return {
      jFront: '',
      jBack: '',
      fc: {},
      next: 0,
      prev: 0
    }
  },
  watch: {
    '$route' (to, from) {
      this.load()
    }
  },
  mounted () {
    this.load()
  },
  methods: {
    save () {
      this.getJSON()
    },
    getJSON () {
      this.jFront = 'save'
      this.jBack = 'save'
    },
    gotJSON () {
      console.log('got json')

      Data.saveFlashcard(this.fc)
      // done saving
    },
    load () {
      Data.getFlashcard(this.$route.params.id).then(fc => {
        console.log('got flashcard' + fc)
        this.jFront = fc.front
        console.log(fc)
        this.jBack = fc.back
        this.fc = fc
      })
      Data.getInSeqFlashcard(this.$route.params.id, true).then((fc) => { this.prev = (fc) ? fc.id : 0 })
      Data.getInSeqFlashcard(this.$route.params.id, false).then((fc) => { this.next = (fc) ? fc.id : 0 })
      // EventBus.$emit('get-json-cards')
      // EventBus.$on('json-cards', (name, json) => window.localStorage.setItem(name, json))
      // console.log(window.localStorage.getItem('card'))
      // this.fabric.loadFromJSON(window.localStorage.getItem('card'))
    },
    jsonReady (ev) {
      if (ev.id === 'front') {
        this.jFront = ev.json
        this.fc.front = this.jFront
        this.fc.frontSVG = ev.svg
      } else {
        this.jBack = ev.json
        this.fc.back = this.jBack
        this.fc.backSVG = ev.svg
      }

      if (this.jFront !== 'save' && this.jBack !== 'save') {
        this.gotJSON()
      }
    }
  }
}
</script>

<style>
</style>



