/*eslint-disable*/
export default class {
  constructor (paper, color) {
    this.paper = paper
    this.path = []
    this.color = color
  }
  onMouseDown (event) {

    // If we produced a path before, deselect it:
    if (this.path) {
      this.path.selected = false
    }
    this.path = new this.paper.Path()
    this.path.add(event.point)
    this.path.strokeColor = this.color

    // Select the path, so we can see its segment points:
  }
  onMouseDrag (event) {
  // Every drag event, add a point to the path at the current
  // position of the mouse:
    if (this.path.lastSegment.point.getDistance(event.point) < 30) {
      this.path.add(event.point)
    }
  }
  onMouseUp (event) {

    // When the mouse is released, simplify it:
    // path.simplify(2.)
    this.path.simplify(1.)
  }
  getTool () {
      var tool = new this.paper.Tool()
      tool.onMouseDown = this.onMouseDown.bind(this)
      tool.onMouseDrag = this.onMouseDrag.bind(this)
      tool.onMouseUp = this.onMouseUp.bind(this)

      return tool
  }
/*eslint-enable */
}



